import React, { Component } from 'react';
import { Container, Jumbotron, Row, Col, Button } from 'react-bootstrap';
import './Home.css';

class Home extends Component {

    constructor(props) {

        super(props);

        this.state = {
            longitude: null,
            latitude: null,
            speed: 0,
            pressed: false,
            firstTime: true
        }

    }

    async componentDidMount() {

        // const url = "https://speedlimit-io.p.rapidapi.com/get_speed_limit?longitude=-75.46798079999999&latitude=39.7279232";
        // const reponse = await fetch(url, {
        //     "headers": {
        //         "x-rapidapi-host": "speedlimit-io.p.rapidapi.com",
        //         "x-rapidapi-key": "2496fa6213msh5b286d3f657429cp173040jsn13b456d4488f"
        //     }
        // })
        // const data = await reponse.json();
        // console.log(data.max_speed);

        // this.position();

        this.initalPosition();


    }

    async initalPosition() {
        await navigator.geolocation.getCurrentPosition(
            position => this.setState({
                latitude: position.coords.latitude,
                longitude: position.coords.longitude
            })
        )
    }


    async position() {

        if (this.state.firstTime === true) {

            this.setState({ firstTime: false })
            
            await navigator.geolocation.getCurrentPosition(
                position => this.setState({
                    latitude: position.coords.latitude,
                    longitude: position.coords.longitude
                })
            )

            const url = `https://speedlimit-io.p.rapidapi.com/get_speed_limit?longitude=${this.state.longitude}&latitude=${this.state.latitude}`;
            const reponse = await fetch(url, {
                "headers": {
                    "x-rapidapi-host": "speedlimit-io.p.rapidapi.com",
                    "x-rapidapi-key": "2496fa6213msh5b286d3f657429cp173040jsn13b456d4488f"
                }
            })
            const data = await reponse.json();
            //console.log(data.max_speed);
            this.setState({ speed: data.max_speed })
            this.setState({ pressed: true })

            this.position()

        } else {

            setInterval(async () => {

                await navigator.geolocation.getCurrentPosition(
                    position => this.setState({
                        latitude: position.coords.latitude,
                        longitude: position.coords.longitude
                    })
                )

                const url = `https://speedlimit-io.p.rapidapi.com/get_speed_limit?longitude=${this.state.longitude}&latitude=${this.state.latitude}`;
                const reponse = await fetch(url, {
                    "headers": {
                        "x-rapidapi-host": "speedlimit-io.p.rapidapi.com",
                        "x-rapidapi-key": "2496fa6213msh5b286d3f657429cp173040jsn13b456d4488f"
                    }
                })
                const data = await reponse.json();
                //console.log(data.max_speed);
                this.setState({ speed: data.max_speed })
                
                // this.setState({ pressed: true })

            }, 5000)
        }

    }



    render() {
        return (
            <div>
                {/* <button onClick={() => this.position()}>Show Speed</button>
                {this.state.speed != 0 &&
                    <div>
                        <h1>{this.state.speed}</h1>
                    </div>
                } */}

                {this.state.pressed === false &&
                    <Button className="content" onClick={() => this.position()}>Show Speed Limit</Button>
                }



                <Container fluid>

                    <Jumbotron className="jumbo">

                        <Row>
                            <Col className="text-center">

                                {this.state.pressed === true &&
                                    <div>
                                        <h1 style={{ color: 'white', marginTop: 150, fontSize: 50 }}>{this.state.speed}</h1>
                                    </div>
                                }
                            </Col>
                        </Row>

                    </Jumbotron>

                </Container>

            </div>
        );
    }
}

export default Home;